<template>
    <div class="index">
     
        <ul ref ='box'>
            <li @click ='next(item,index)' v-for ='(item,index) in list ' :key ='index'>
                <img :src="item" alt="">
                <div class="big">
                    <img :src="item" alt="">
                </div>
            </li>
        </ul>
           <audio ref ='audio' src="./static/你懂得.mp3" autoplay></audio>
    </div>
</template>
<script>
export default {
    methods:{
        next(item,index){
            
            this.$refs.audio.play()
            let li = document.getElementsByTagName('li')
             li[index].children[1].style.transform = 'scale(0)'
                li[index].children[0].style.filter = 'blur(0)'
            setTimeout(()=>{
                  index++
           if(index == this.list.length-1){
            this.$refs.box.style.marginLeft = '0%'
            this.chongzhi()
              
             return 
           }else {
              
           } 
            this.$refs.box.style.marginLeft = -(index *100) + '%'
               this.bigactive(index)
            },300)
        },
        bigactive(index){
let li = document.getElementsByTagName('li')
setTimeout(()=>{
    li[index].children[1].style.transform = 'scale(1)'
    li[index].children[0].style.filter = 'blur(10px)'
},600)
        },
        chongzhi(){
            let li = document.getElementsByTagName('li')
            for(let i = 0;i<li.length;i++){
                li[i].children[1].style.transform = 'scale(0)'
                li[i].children[0].style.filter = 'blur(0)'
            }
             setTimeout(()=>{
                li[0].children[1].style.transform = 'scale(1)'
             li[0].children[0].style.filter = 'blur(10px)'
           },500)
        }
    },
    mounted(){
        document.addEventListener('touchmove', function(event) {
    event.preventDefault();
}, false);
         let li = document.getElementsByTagName('li')
         let len = (this.list.length * 100) + '%'
         console.log(len)
         this.$refs.box.style.width = len
           setTimeout(()=>{
                li[0].children[1].style.transform = 'scale(1)'
             li[0].children[0].style.filter = 'blur(10px)'
           },500)
    },
    data(){
        return {
            list : [
                './static/01.jpg',
                './static/02.jpg',
                './static/03.jpg',
                './static/04.jpg',
                './static/05.jpg',
                './static/06.jpg',
                './static/07.jpg',
                './static/08.jpg',
                './static/09.jpg',
                './static/10.jpg',
                './static/11.jpg',
                './static/12.jpg',
                './static/13.jpg',
                './static/14.jpg',
                './static/15.jpg',
                './static/16.jpg'

    ]
        } 
    }
}
</script>
<style scoped>
.index{
    height: 100%;
    width: 100%;
    overflow:hidden;
    position: fixed;
    top: 0;
    right:0;
    bottom: 0;
    left: 0;
}

ul{
    height: 100%;
    display: flex;
    transition: all .3s ease;
}
img{
    width: 100%;
    height: 100%;
    position: absolute;
}

li{
    height: 100%;
    width: 100%;
}

.big{
    width: 80%;
    height: 80%;
    position: absolute;
    z-index: 10;
    margin-top: 20%;
    margin-left: 10%;
    transform: scale(0);
    transform-origin: center center;
    transition: all .2s ease;
}
</style>


